.modes{
    background: url(../../img/main/bg-main.png) no-repeat;
    background-size: contain;
    margin-bottom: 177px;

    @include phones{
        margin-bottom: 40px;
    }

    &__content-block{
        display: flex;

        @include tablets{
            flex-direction: column;
        }
    }

    &__col{
        &:first-child{
            max-width: 370px;
            flex-shrink: 0;
            margin-top: 81px;

            @include tablets{
                margin-bottom: 20px;
            }

            @include phones{
                margin-bottom: 46px;
            }
        }

        &:last-child{
            margin-left: 110px;

            @include tablets{
                margin-left: 0;
                margin-bottom: 0;
            }
        }
    }

    &__title{
        margin-bottom: 23px;
        position: relative;
        display: block;

        &:before{
            content: '';
            display: inline-block;
            position: absolute;
            width: 157px;
            height: 476px;
            background: url(../../img/figure/modes.png)no-repeat;   
            top: 50%;
            left: 100%;
            margin-left: -14px;
            transform: translateX(-50%);
        }
    }

    &__text{
        margin-bottom: 20px;
    }

    &__list{
        margin-bottom: 68px;
    }

    &__options{
        display: flex;
        
        @include phones{
            justify-content: space-between;
        }

        &-block{
            margin-right: 73px;

            &:last-child{
                margin-right: 0;
            }

            @include phones{
                margin-right: 0;
            }
        }

        &-row{
            display: flex;
            margin-bottom: 5px;
        }

        &-value , &-subvalue{
            letter-spacing: -4px;
            color: #3a30f8;
            font-family: "Rubik Mono One";
            font-weight: 400;

            @include phones{
                font-size: 42px;
            }
        }

        &-value{
            font-size: 60px;
        }

        &-subvalue{
            font-size: 26px;
            align-self: flex-end;
            margin-left: 7px;
            margin-bottom: 9px;

            @include phones{
                font-size: 20px;
            }
        }

        &-text{
            color: #8c8c8c;
            font-size: 16px;
            line-height: 23px;
        }
    }

    @include tablets{
        padding: 0 3%;
    }
}

@media screen and (max-width:1000px){
    .modes{
        &__img{
            height: 100%;
            width: 100%;
            object-fit: cover;
        }

        &__title{
            &:before{
                display: none;
            }
        }
    }
}