.list-number{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    position: fixed;
    right: 77px;
    top: 50%;
    transform: translateY(-50%);

    &:after , &:before{
        content:'';
        display: inline-block;
        height: 320px;
        width: 1px;
        background-color: #392ff7;
    }

    &:before{
        margin-bottom: 15px;
    }

    &:after{
        margin-top: 15px;
    }

    &__item{
        margin-bottom: 20px;

        &:last-child{
            margin-bottom: 0;
        }

        &--active{
            .list-number__link{
                border-color: #392ff7;

                &:before{
                    visibility: hidden;
                }
            }
        }
    }

    &__link{
        display: inline-block;
        width: 16px;
        height: 16px;
        border-radius: 50%;
        border: 2px solid transparent;
        position: relative;

        &::before{
            content: '';
            display: inline-block;
            height: 7px;
            width: 7px;
            border-radius: 50%;
            background-color: #392ff7;

            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
        }

        &:hover{
            border-color: #392ff7;

            &:before{
                visibility: hidden;
            }
        }
    }
    
    @include tablets{
        display: none;
    }
}

@media screen and (max-width:1300px){
    .list-number{
        right: 25px;
    }
}