body {
  margin: 0;
  padding: 0;
  font-size: 14px;
  font-family: "Roboto";
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  outline: none;
  border: 1px solid transparent;
  cursor: pointer;
}

p {
  margin: 0;
}

.wrapper {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
  background-color: #fff;
}

.container {
  width: 1170px;
  max-width: 100%;
  margin: 0 auto;
}

.title {
  font-size: 40px;
  letter-spacing: 3px;
  color: #000000;
  font-family: "Rubik Mono One";
  font-weight: 400;
  text-transform: uppercase;
}

.subtitle {
  font-size: 20px;
  letter-spacing: 2px;
  // line-height: 80px;
  color: #3a30f8;
  font-family: "Roboto";
  font-weight: 700;
  text-transform: uppercase;
}

.text {
  font-size: 18px;
  line-height: 30px;
  color: #000000;
  font-family: "Roboto";
  font-weight: 300;
}

.button {
  font-size: 16px;
  letter-spacing: 1px;
  color: #3a30f8;
  font-weight: 700;
  text-transform: uppercase;
  padding: 15px 41px;

  border-radius: 25px;
  filter: drop-shadow(0px 5px 2.5px rgba(0, 0, 1, 0.2));
  background-color: #ffffff;

  transition: 0.4s;

  &:hover {
    opacity: 0.7;
  }
}

.body--active-menu {
  overflow: hidden;
}

