.burger {
  position: fixed;
  top: 41px;
  right: 3%;
  height: 25px;
  width: 30px;
  cursor: pointer;
  z-index: 15;
  transition: all 0.4s;
  display: none;

  &__line {
    width: 100%;
    height: 2px;
    position: absolute;
    background-color: black;
    top: 0;
    left: 0;
    transition: all 0.4s;

    &:nth-child(2) {
      top: 50%;
      transform: translateY(-50%);
    }

    &:nth-child(3) {
      bottom: 0;
      top: initial;
    }
  }
}

@media screen and (max-width:768px){
  .burger{
    display: inline-block;
  }
}

.burger--active{
  .burger__line{
    &:nth-child(1){
      opacity:0;
    }
    
    &:nth-child(2){
      transform:rotate(45deg);
    }
    
    &:nth-child(3){
      transform:rotate(-45deg) translateY(-8px) translateX(5px);
    }
  }
}
