.advantages {
  margin-top: 197px;

  .container {
    display: flex;
  }

  &__col {
    &:first-child {
      position: relative;
    }
  }

  &__subtitle {
    margin-bottom: 79px;
  }

  &__title {
    margin-bottom: 22px;
    position: relative;

    &--line {
      &:before {
        content: "";
        display: inline-block;
        position: absolute;
        width: 19px;
        height: 291px;
        background: url(../../img/figure/gh.png) no-repeat;
        left: -51px;
        top: 19px;
      }
    }
  }

  &__text {
    margin-bottom: 20px;
  }

  &__colors-form {
    margin-left: 21px;
  }

  &__current-meh {
    margin-top: 33px;
    margin-left: -104px;

  }

  &__video {
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    display: inline-block;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-color: #ffffff;
    cursor: pointer;

    &:before {
      content: "";
      display: inline-block;
      position: absolute;
      top: 50%;
      left: 55%;
      transform: translate(-50%, -50%);
      background: url(../../img/video/video-m.png) center center no-repeat;
      background-size: contain;
      width: 45px;
      height: 52px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .advantages {
    &__col {
      &:last-child {
        flex-direction: column;
      }
    }

    &__content-block {
      margin-bottom: 20px;

      &:last-child {
        margin-left: 0 !important;
        margin-top: 0 !important;
        margin-bottom: 0;
      }
    }
  }
}

@media screen and (max-width: 880px) {
  .advantages {
    margin-top:60px;

    .container {
      flex-direction: column;
    }

    &__img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &__col {
      &:first-child {
        margin-left: 0 !important;
      }

      &:last-child {
        margin-left: 0 !important;
        margin-top: 20px !important;
        align-items: center;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .advantages {
    &__subtitle {
      margin-bottom: 24px;
    }
  }
}

@media screen and (max-width: 480px) {
  .advantages {
    padding:0 3%;

    &__subtitle {
      margin-bottom: 24px;
    }

    &__content-block {
      width: 100% !important;
    }

    &__current-meh{
        margin-left: 0;
    }
  }
}

@media screen and (max-width: 320px) {
    .advantages {  
      &__current-meh-img{
          height: 100%;
          width: 100%;     
      }
    }
  }
