.menu{
    &__list{
        display: flex;
        align-items: center;
    }

    &__item{
        margin-right: 71px;
        position: relative;

        &:after{
            content: '';
            display: inline-block;
            width: 19px;
            height: 1px;
            background-color: #3a30f8;
            visibility: hidden;

            position: absolute;
            bottom: -2px;
            left: -7px;
        }

        &:last-child{
            margin-right: 0;
        }

        &--active{
           &:after{
                visibility: visible;
           } 
        }

        &:hover{
            &:after{
                visibility: visible;
            }
        }
    }

    &__link{
        color: #000000;
        font-weight: 700;
        text-transform: uppercase;
        transition: .4s;
        letter-spacing: 1px;

        &:hover{
            opacity: .7;
        }
    }

    @include tablets{
        display: none;
    }
}