.for-whom{
    // background-color: #3a30f8;
    background: url(../../img/bg/for-whom.png) no-repeat;
    background-size: cover;
    padding-bottom: 98px;

    &__content{
        display: flex;
    }

    &__col{
        &:first-child{
            flex-shrink: 0;
            margin-top: 96px;
        }

        &:last-child{
            margin-left: 38px;
            margin-top: -60px;
            position: relative;
        }
    }
    
    &__subtitle{
        margin-bottom: 78px;

        &.subtitle{
            color: #ffffff;
        }
    }

    &__text{
        margin-bottom: 123px;
        max-width: 330px;
        display: inline-block;

        &.text{
            color: #ffffff;
        }
    }

    &__img{

        &-text{
            position: absolute;
            bottom: -40px;
            left: 0;

            display: flex;
            flex-direction: column;

            &-span{
                font-size: 186px;
                letter-spacing: -46px;
                line-height: 183px;
                color: #ffffff;
                font-family: "Rubik Mono One";
                font-weight: 400;
                opacity: .5;

                &:first-child{
                    align-self: flex-end;
                    margin-right: 53px;
                }

                &:last-child{
                    letter-spacing: 10px;
                }
            }
        }
    }
}

@media screen and (max-width: 1200px){
    .for-whom{
        &__img{
            height: 100%;
            width: 100%;
            object-fit: contain;

            &-text-span{
                font-size: 110px;
                line-height: 110px;
            }
        }
    }
}


@media screen and (max-width: 900px){
    .for-whom{
        &__img{
            &-text-span{
                font-size: 95px;
                line-height: 85px;

                &:first-child{
                    letter-spacing: initial;
                }
            }
        }
    }
}

@media screen and (max-width: 768px){
    .for-whom{
        &__content{
            flex-direction: column;
        }

        &__col{
            margin-bottom: 20px;

            &:first-child{
                text-align: center;
            }

            &:last-child{
                margin-top: 0;
                margin-bottom: 0;

                // margin-left: 0;
            }
        }

        &__img-text{
            left: 50%;
            transform: translateX(-50%);
        }

        &__subtitle {
            margin-bottom: 35px;
        }

        &__text {
            margin-bottom: 47px;
        }
    }
}

@media screen and (max-width: 480px){
    .for-whom{
        padding: 30px 0;

        &__content{
            flex-direction: column-reverse;
        }

        &__col{
            &:first-child{
                margin-bottom: 0;
            }

            &:last-child{
                margin-left: 0;
            }
        }

        &__img-text-span{
            font-size: 40px;
            line-height: 40px;
            opacity: 1;
        }
    }
}