.advantages {
  margin-top: 197px;
}

.advantages .container {
  display: flex;
}

.advantages__col:first-child {
  position: relative;
}

.advantages__subtitle {
  margin-bottom: 79px;
}

.advantages__title {
  margin-bottom: 22px;
  position: relative;
}

.advantages__title--line:before {
  content: "";
  display: inline-block;
  position: absolute;
  width: 19px;
  height: 291px;
  background: url(../../img/figure/gh.png) no-repeat;
  left: -51px;
  top: 19px;
}

.advantages__text {
  margin-bottom: 20px;
}

.advantages__colors-form {
  margin-left: 21px;
}

.advantages__current-meh {
  margin-top: 33px;
  margin-left: -104px;
}

.advantages__video {
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: inline-block;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: #ffffff;
  cursor: pointer;
}

.advantages__video:before {
  content: "";
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 55%;
  transform: translate(-50%, -50%);
  background: url(../../img/video/video-m.png) center center no-repeat;
  background-size: contain;
  width: 45px;
  height: 52px;
}

.burger {
  position: fixed;
  top: 41px;
  right: 3%;
  height: 25px;
  width: 30px;
  cursor: pointer;
  z-index: 15;
  transition: all 0.4s;
  display: none;
}

.burger__line {
  width: 100%;
  height: 2px;
  position: absolute;
  background-color: black;
  top: 0;
  left: 0;
  transition: all 0.4s;
}

.burger__line:nth-child(2) {
  top: 50%;
  transform: translateY(-50%);
}

.burger__line:nth-child(3) {
  bottom: 0;
  top: initial;
}

.burger--active .burger__line:nth-child(1) {
  opacity: 0;
}

.burger--active .burger__line:nth-child(2) {
  transform: rotate(45deg);
}

.burger--active .burger__line:nth-child(3) {
  transform: rotate(-45deg) translateY(-8px) translateX(5px);
}

.colors-list {
  display: flex;
  align-items: center;
}

.colors-list__item {
  margin-right: 34px;
  display: inline-block;
  height: 17px;
  width: 17px;
  border-radius: 50%;
  cursor: pointer;
  border: 1px solid transparent;
  position: relative;
  transition: .4s;
}

.colors-list__item:last-child {
  margin-right: 0;
}

.colors-list__item:hover {
  transform: scale(1.1);
}

.colors-list__item--active:after {
  content: '';
  display: inline-block;
  width: 1px;
  height: 50px;
  background-color: #cdcdcd;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 15px;
}

.content-list__item {
  margin-bottom: 20px;
  display: flex;
}

.content-list__item:before {
  content: '';
  display: inline-block;
  height: 5px;
  width: 30px;
  background-color: #000000;
  margin-right: 14px;
  margin-top: 13px;
}

.content-list__item:last-child {
  margin-bottom: 0;
}

.footer {
  background-color: #292929;
  padding: 7px 0;
}

.footer__text {
  font-size: 12px;
  color: #605f5f;
  font-weight: 300;
  text-align: center;
}

.for-whom {
  background: url(../../img/bg/for-whom.png) no-repeat;
  background-size: cover;
  padding-bottom: 98px;
}

.for-whom__content {
  display: flex;
}

.for-whom__col:first-child {
  flex-shrink: 0;
  margin-top: 96px;
}

.for-whom__col:last-child {
  margin-left: 38px;
  margin-top: -60px;
  position: relative;
}

.for-whom__subtitle {
  margin-bottom: 78px;
}

.for-whom__subtitle.subtitle {
  color: #ffffff;
}

.for-whom__text {
  margin-bottom: 123px;
  max-width: 330px;
  display: inline-block;
}

.for-whom__text.text {
  color: #ffffff;
}

.for-whom__img-text {
  position: absolute;
  bottom: -40px;
  left: 0;
  display: flex;
  flex-direction: column;
}

.for-whom__img-text-span {
  font-size: 186px;
  letter-spacing: -46px;
  line-height: 183px;
  color: #ffffff;
  font-family: "Rubik Mono One";
  font-weight: 400;
  opacity: .5;
}

.for-whom__img-text-span:first-child {
  align-self: flex-end;
  margin-right: 53px;
}

.for-whom__img-text-span:last-child {
  letter-spacing: 10px;
}

.fullscreen {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 100%;
  z-index: 10;
  height: 100%;
  width: 100%;
  background-color: #fff;
  transition: all 0.4s;
}

.fullscreen .menu {
  display: block;
}

.fullscreen .menu__list {
  flex-direction: column;
  align-items: center;
}

.fullscreen .menu__item {
  margin-right: 0;
  margin-bottom: 20px;
}

.fullscreen .menu__item:last-child {
  margin-bottom: 0;
}

.fullscreen .menu__item--active:after {
  visibility: hidden;
}

.fullscreen .menu__link {
  color: black;
  font-size: 33px;
}

.fullscreen--active {
  left: 0;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.list-number {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: fixed;
  right: 77px;
  top: 50%;
  transform: translateY(-50%);
}

.list-number:after,
.list-number:before {
  content: '';
  display: inline-block;
  height: 320px;
  width: 1px;
  background-color: #392ff7;
}

.list-number:before {
  margin-bottom: 15px;
}

.list-number:after {
  margin-top: 15px;
}

.list-number__item {
  margin-bottom: 20px;
}

.list-number__item:last-child {
  margin-bottom: 0;
}

.list-number__item--active .list-number__link {
  border-color: #392ff7;
}

.list-number__item--active .list-number__link:before {
  visibility: hidden;
}

.list-number__link {
  display: inline-block;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  border: 2px solid transparent;
  position: relative;
}

.list-number__link::before {
  content: '';
  display: inline-block;
  height: 7px;
  width: 7px;
  border-radius: 50%;
  background-color: #392ff7;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.list-number__link:hover {
  border-color: #392ff7;
}

.list-number__link:hover:before {
  visibility: hidden;
}

.logo__link {
  display: inline-block;
}

.look-video {
  position: absolute;
  bottom: 0;
  left: 0;
  transform: rotate(-90deg);
}

.look-video__text {
  font-size: 12px;
  letter-spacing: 1px;
  color: #000000;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: -10px;
}

.look-video__img {
  transform: rotate(90deg);
  cursor: pointer;
  animation: pulse .5s linear alternate infinite;
}

.look-video__img-rect {
  max-width: 100%;
  max-height: 100%;
  margin-top: 11px;
}

.look-video:before {
  content: '';
  display: inline-block;
  position: absolute;
}

.look-video--main:before {
  width: 18px;
  height: 74px;
  background: url(../../img/figure/main.png) no-repeat;
  transform: rotate(90deg);
  left: -55px;
  top: -36px;
}

.look-video--modes {
  bottom: 68px;
  left: -35px;
}

@keyframes pulse {
  from {
    transform: rotate(90deg) scale(1);
  }

  to {
    transform: rotate(90deg) scale(1.1);
  }
}

.main {
  background: url(../../img/main/bg-main.png) no-repeat;
  background-size: contain;
  padding-top: 30px;
}

.main__content {
  display: flex;
  margin-top: 29px;
}

.main__col:last-child {
  margin-left: -196px;
  position: relative;
}

.main__col:last-child .look-video {
  bottom: auto;
  top: 37px;
  left: -44px;
}

.main__col--zindex {
  z-index: 2;
  position: relative;
  margin-top: 184px;
}

.main__title-name {
  font-family: 'Rubik Mono One', sans-serif;
  font-size: 80px;
  letter-spacing: 6px;
  color: #000000;
  font-weight: 400;
  line-height: 96px;
}

.main__title-name span {
  letter-spacing: -12px;
}

.main__description {
  font-size: 20px;
  letter-spacing: 1.5px;
  line-height: 30px;
  color: #000000;
  font-weight: 700;
  text-transform: uppercase;
  margin-top: 18px;
  margin-left: 5px;
}

.main__arrow {
  display: inline-block;
  cursor: pointer;
  margin-top: 35px;
  margin-left: 35px;
}

.main__arrow:before {
  content: '';
  display: inline-block;
  height: 95px;
  width: 11px;
  background: url(../../img/arrow.png) center no-repeat;
  transition: all .4s;
  animation: arrow .4s linear infinite;
  animation-direction: alternate;
}

.main__arrow:hover::before {
  opacity: .6;
  animation: none;
}

@keyframes arrow {
  from {
    transform: translateY(0);
  }

  to {
    transform: translateY(10px);
  }
}

.menu__list {
  display: flex;
  align-items: center;
}

.menu__item {
  margin-right: 71px;
  position: relative;
}

.menu__item:after {
  content: '';
  display: inline-block;
  width: 19px;
  height: 1px;
  background-color: #3a30f8;
  visibility: hidden;
  position: absolute;
  bottom: -2px;
  left: -7px;
}

.menu__item:last-child {
  margin-right: 0;
}

.menu__item--active:after {
  visibility: visible;
}

.menu__item:hover:after {
  visibility: visible;
}

.menu__link {
  color: #000000;
  font-weight: 700;
  text-transform: uppercase;
  transition: .4s;
  letter-spacing: 1px;
}

.menu__link:hover {
  opacity: .7;
}

.modes {
  background: url(../../img/main/bg-main.png) no-repeat;
  background-size: contain;
  margin-bottom: 177px;
}

.modes__content-block {
  display: flex;
}

.modes__col:first-child {
  max-width: 370px;
  flex-shrink: 0;
  margin-top: 81px;
}

.modes__col:last-child {
  margin-left: 110px;
}

.modes__title {
  margin-bottom: 23px;
  position: relative;
  display: block;
}

.modes__title:before {
  content: '';
  display: inline-block;
  position: absolute;
  width: 157px;
  height: 476px;
  background: url(../../img/figure/modes.png) no-repeat;
  top: 50%;
  left: 100%;
  margin-left: -14px;
  transform: translateX(-50%);
}

.modes__text {
  margin-bottom: 20px;
}

.modes__list {
  margin-bottom: 68px;
}

.modes__options {
  display: flex;
}

.modes__options-block {
  margin-right: 73px;
}

.modes__options-block:last-child {
  margin-right: 0;
}

.modes__options-row {
  display: flex;
  margin-bottom: 5px;
}

.modes__options-value,
.modes__options-subvalue {
  letter-spacing: -4px;
  color: #3a30f8;
  font-family: "Rubik Mono One";
  font-weight: 400;
}

.modes__options-value {
  font-size: 60px;
}

.modes__options-subvalue {
  font-size: 26px;
  align-self: flex-end;
  margin-left: 7px;
  margin-bottom: 9px;
}

.modes__options-text {
  color: #8c8c8c;
  font-size: 16px;
  line-height: 23px;
}

body {
  margin: 0;
  padding: 0;
  font-size: 14px;
  font-family: "Roboto";
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  outline: none;
  border: 1px solid transparent;
  cursor: pointer;
}

p {
  margin: 0;
}

.wrapper {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
  background-color: #fff;
}

.container {
  width: 1170px;
  max-width: 100%;
  margin: 0 auto;
}

.title {
  font-size: 40px;
  letter-spacing: 3px;
  color: #000000;
  font-family: "Rubik Mono One";
  font-weight: 400;
  text-transform: uppercase;
}

.subtitle {
  font-size: 20px;
  letter-spacing: 2px;
  color: #3a30f8;
  font-family: "Roboto";
  font-weight: 700;
  text-transform: uppercase;
}

.text {
  font-size: 18px;
  line-height: 30px;
  color: #000000;
  font-family: "Roboto";
  font-weight: 300;
}

.button {
  font-size: 16px;
  letter-spacing: 1px;
  color: #3a30f8;
  font-weight: 700;
  text-transform: uppercase;
  padding: 15px 41px;
  border-radius: 25px;
  filter: drop-shadow(0px 5px 2.5px rgba(0, 0, 1, 0.2));
  background-color: #ffffff;
  transition: 0.4s;
}

.button:hover {
  opacity: 0.7;
}

.body--active-menu {
  overflow: hidden;
}

@media screen and (max-width: 1300px) {
  .list-number {
    right: 25px;
  }
}

@media screen and (max-width: 1200px) {
  .advantages__col:last-child {
    flex-direction: column;
  }

  .advantages__content-block {
    margin-bottom: 20px;
  }

  .advantages__content-block:last-child {
    margin-left: 0 !important;
    margin-top: 0 !important;
    margin-bottom: 0;
  }

  .for-whom__img {
    height: 100%;
    width: 100%;
    object-fit: contain;
  }

  .for-whom__img-text-span {
    font-size: 110px;
    line-height: 110px;
  }
}

@media screen and (max-width: 1000px) {
  .modes__img {
    height: 100%;
    width: 100%;
    object-fit: cover;
  }

  .modes__title:before {
    display: none;
  }
}

@media screen and (max-width: 900px) {
  .for-whom__img-text-span {
    font-size: 95px;
    line-height: 85px;
  }

  .for-whom__img-text-span:first-child {
    letter-spacing: initial;
  }
}

@media screen and (max-width: 880px) {
  .advantages {
    margin-top: 60px;
  }

  .advantages .container {
    flex-direction: column;
  }

  .advantages__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .advantages__col:first-child {
    margin-left: 0 !important;
  }

  .advantages__col:last-child {
    margin-left: 0 !important;
    margin-top: 20px !important;
    align-items: center;
  }
}

@media screen and (max-width: 768px) {
  .advantages__subtitle {
    margin-bottom: 24px;
  }

  .burger {
    display: inline-block;
  }

  .for-whom__content {
    flex-direction: column;
  }

  .for-whom__col {
    margin-bottom: 20px;
  }

  .for-whom__col:first-child {
    text-align: center;
  }

  .for-whom__col:last-child {
    margin-top: 0;
    margin-bottom: 0;
  }

  .for-whom__img-text {
    left: 50%;
    transform: translateX(-50%);
  }

  .for-whom__subtitle {
    margin-bottom: 35px;
  }

  .for-whom__text {
    margin-bottom: 47px;
  }

  .list-number {
    display: none;
  }

  .look-video--main:before {
    display: none;
  }

  .look-video--modes {
    left: auto;
    right: 26px;
    bottom: auto;
    top: -63px;
  }

  .main__content {
    flex-direction: column-reverse;
  }

  .main__col:last-child .look-video {
    bottom: -59px;
    right: 20px;
    top: auto;
    left: auto;
  }

  .main__col:last-child {
    margin-left: 0;
  }

  .main__col--zindex {
    margin-top: 30px;
    text-align: center;
  }

  .main__col-img {
    height: 100%;
    width: 100%;
    object-fit: cover;
  }

  .main__title {
    text-align: left;
  }

  .main__title-name {
    font-size: 70px;
    line-height: 80px;
  }

  .main__arrow {
    margin-left: 0;
  }

  .main {
    padding: 30px 3% 0 3%;
  }

  .menu {
    display: none;
  }

  .modes__content-block {
    flex-direction: column;
  }

  .modes__col:first-child {
    margin-bottom: 20px;
  }

  .modes__col:last-child {
    margin-left: 0;
    margin-bottom: 0;
  }

  .modes {
    padding: 0 3%;
  }
}

@media screen and (max-width: 630px) {
  .main__title-name {
    font-size: 45px;
    line-height: 65px;
  }
}

@media screen and (max-width: 480px) {
  .advantages {
    padding: 0 3%;
  }

  .advantages__subtitle {
    margin-bottom: 24px;
  }

  .advantages__content-block {
    width: 100% !important;
  }

  .advantages__current-meh {
    margin-left: 0;
  }

  .for-whom {
    padding: 30px 0;
  }

  .for-whom__content {
    flex-direction: column-reverse;
  }

  .for-whom__col:first-child {
    margin-bottom: 0;
  }

  .for-whom__col:last-child {
    margin-left: 0;
  }

  .for-whom__img-text-span {
    font-size: 40px;
    line-height: 40px;
    opacity: 1;
  }

  .main__title {
    text-align: center;
  }

  .main__title-name {
    font-size: 30px;
    line-height: 45px;
  }

  .modes {
    margin-bottom: 40px;
  }

  .modes__col:first-child {
    margin-bottom: 46px;
  }

  .modes__options {
    justify-content: space-between;
  }

  .modes__options-block {
    margin-right: 0;
  }

  .modes__options-value,
  .modes__options-subvalue {
    font-size: 42px;
  }

  .modes__options-subvalue {
    font-size: 20px;
  }
}

@media screen and (max-width: 320px) {
  .advantages__current-meh-img {
    height: 100%;
    width: 100%;
  }
}