.colors-list{
    display: flex;
    align-items: center;

    &__item{
        margin-right: 34px;
        display: inline-block;
        height: 17px;
        width: 17px;
        border-radius: 50%;
        cursor: pointer;
        border:1px solid transparent;
        position: relative;
        transition: .4s;

        &:last-child{
            margin-right: 0;
        }

        &:hover{
            transform: scale(1.1);
        }

        &--active{
            &:after{
                content:'';
                display: inline-block;
                width: 1px;
                height: 50px;
                background-color: #cdcdcd;

                position: absolute;
                top: 100%;
                left: 50%;
                transform: translateX(-50%);
                margin-top: 15px;
            }
        }
    }
}