.look-video{
    position: absolute;
    bottom: 0;
    left: 0;
    transform: rotate(-90deg);
    

    &__text{
        font-size: 12px;
        letter-spacing: 1px;
        color: #000000;
        font-weight: 700;
        text-transform: uppercase;
        margin-bottom: -10px;
    }

    &__img{
        transform: rotate(90deg);
        cursor: pointer;
        animation: pulse .5s linear alternate infinite;

        &-rect{
            max-width: 100%;
            max-height: 100%;
            margin-top: 11px;
        }
    }

    &:before{
        content: '';
        display: inline-block;
        position: absolute;
    }

    &--main{
        &:before{
            width: 18px;
            height: 74px;
            background: url(../../img/figure/main.png) no-repeat;
            transform: rotate(90deg);
            left: -55px;
            top: -36px;

            @include tablets{
                display: none;
            }
        }
    }

    &--modes{
        bottom: 68px;
        left: -35px;

        @include tablets{
            left: auto;
            right: 26px;
            bottom: auto;
            top: -63px;
        }
    }

}


@keyframes pulse{
    from{
        transform:rotate(90deg) scale(1) ;
    }

    to{
        transform:rotate(90deg) scale(1.1) ;
    }
}