.main{
    background: url(../../img/main/bg-main.png) no-repeat;
    background-size: contain;
    padding-top: 30px;

    &__content{
        display: flex;
        margin-top: 29px;

        @include tablets{
            flex-direction: column-reverse;
        }
    }

    &__col{
        &:last-child{
            margin-left: -196px;
            position: relative;

            .look-video{
                bottom: auto;
                top: 37px;
                left: -44px;

                @include tablets{
                    bottom: -59px;
                    right: 20px;
                    top: auto;
                    left: auto;
                }
            }

            @include tablets{
                margin-left: 0;
            }
        }

        &--zindex{
            z-index: 2;
            position: relative;
            margin-top: 184px;

            @include tablets{
                margin-top: 30px;
                text-align: center;
            }
        }

        &-img{
            @include tablets{
                height: 100%;
                width: 100%;
                object-fit: cover;
            }
        }
    }

    &__title{
        @include tablets{
            text-align: left;
        }

        @include phones{
            text-align: center;
        }

        &-name{
            font-family: 'Rubik Mono One', sans-serif;
            font-size: 80px;
            letter-spacing: 6px;
            color: #000000;
            font-weight: 400;
            line-height: 96px;

            span{
                letter-spacing: -12px;
            }

            @include tablets{
                font-size: 70px;
                line-height: 80px;
            }

            @include phones{
                font-size: 30px;
                line-height: 45px;
            }
        }
    }

    &__description{
        font-size: 20px;
        letter-spacing: 1.5px;
        line-height: 30px;
        color: #000000;
        font-weight: 700;
        text-transform: uppercase;

        margin-top: 18px;
        margin-left: 5px;
    }

    &__arrow{
        display: inline-block;
        cursor: pointer;
        margin-top: 35px;
        margin-left: 35px;
        

        &:before{
            content:'';
            display:inline-block;
            height: 95px;
            width: 11px;
            background:url(../../img/arrow.png) center no-repeat;
            transition: all .4s;
            animation: arrow .4s linear infinite;
            animation-direction: alternate;
        }

        &:hover::before{
            opacity: .6;
            animation: none;
        }

        @include tablets{
            margin-left: 0;
        }
    }

    @include tablets{
        padding: 30px 3% 0 3%;
    }

    @keyframes arrow {
        from{
            transform: translateY(0);
        }

        to{
            transform: translateY(10px);
        }
    }
}

@media screen and (max-width: 630px){
    .main{
        &__title{
            &-name{
                font-size: 45px;
                line-height: 65px;
            }
        }
    }
}