.content-list{
    &__item{
        margin-bottom: 20px;
        display: flex;

        &:before{
            content:'';
            display: inline-block;
            height: 5px;
            width: 30px;
            background-color: #000000;
            margin-right: 14px;
            margin-top: 13px;
        }

        &:last-child{
            margin-bottom: 0;
        }
    }
}
