.fullscreen {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 100%;
  z-index: 10;
  height: 100%;
  width: 100%;
  background-color: #fff;
  transition: all 0.4s;

  .menu{
      display: block;

      &__list {
        flex-direction: column;
        align-items: center;
      }

      &__item {
        margin-right: 0;
        margin-bottom: 20px;
    
        &:last-child {
          margin-bottom: 0;
        }
    
        &--active:after {
          visibility: hidden;
        }
      }

      &__link {
        color: black;
        font-size: 33px;
      }
  }
}

.fullscreen--active{
    left: 0;
}
